/****************************
****** CONTENT BLOG *********
*****************************/

.post{
  position: relative;
  display: flow-root;
  margin: 0 0 10em;
  width: 100%;
}

/* INTRO STYLE */
.post-intro{
  position: relative;
  width: 100%;
  padding: 20em 0 15em;
  z-index: 10;

  /*TAB*/
  @media (--rwd-sm){
    padding: 2em 0 5em;
  }
  /*ORD*/
  @media (--rwd-md){
    min-height: 115vmin;
  }
}

.intro__illu{
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  background: var(--color-default);
  overflow: hidden;

  /*ORD*/
  /*@media (--rwd-md){
    width: calc(var(--col) * 13);
  }*/
}

.intro__illu--part{
  /*ORD*/
  @media (--rwd-md){
    width: calc(var(--col) * 13);
    /*top: 5%;
    left: calc(var(--colr) * 1);*/
  }
}

.intro__header{
  position: relative;
  width: calc(var(--col) * 18);
  left: calc(var(--col) * 1);
  padding: 10% 10% 4% var(--colr);
  box-sizing: border-box;
  background: var(--color-relief);
  color: var(--color-wording);

  /*TAB*/
  @media (--rwd-sm){
    width: calc(var(--col) * 10);
    left: calc(var(--col) * 9);
    padding: 8% 7% 4% 5%;
  }

  /*ORD*/
  @media (--rwd-md){
    /*position: absolute;*/
    margin-top: 5%;
    width: calc(var(--col) * 8);
    left: calc(var(--col) * 10);
  }
}

/* TEXT STYLE */
.intro__header__title{
  @mixin text-title-long;
}
.intro__header__subtitle{
  @mixin text-content;
  text-transform: none;
}
.intro__header__infos{
  display: block;
  @mixin text-subtitle;
  margin: 1.4em 0 5em;

  & > a{ color: inherit; }
  & > time{ text-transform: capitalize; }
}


/****************************
****** INFO PART BLOG *********
*****************************/

/* PART STYLE */
.post-info{
  padding: 8em 0 4em;
  z-index: 10;

  /*TAB*/
  @media (--rwd-sm){
    width: calc(var(--colr) * 14);
    margin-left: calc(var(--colr) * 3);
    margin-top: -5%;
  }
}

.info-list{
  list-style: none;
  display: flex;
  flex-direction: column;
  width: 100%;
  float: none;


  /*TAB*/
  @media (--rwd-sm){
    flex-direction: row;
  }
}

/* INDIQUATEURS STYLE */
.info-item{
  margin-right: calc(var(--col) * 2 );
  line-height: 1.2;
  padding-left: calc(var(--col) * 2);
  margin-bottom: 3.5em;

  /*TAB*/
  @media (--rwd-sm){
    padding-left: 0;
  }

  /*ORD*/
  @media (--rwd-md){
    line-height: 1;
    margin-right: var(--colr);
  }

  /* SUJET DE L'ARTCL */
  &:first-child{
    /*TAB*/
    @media (--rwd-sm){
      order: 3;
      padding-right: calc(var(--colr) * 2);
    }

    /*ORD*/
    @media (--rwd-md){
      padding-right: calc(var(--colr) * 3);
    }
  }

  /* TIME TO READ */
  &:nth-child(2){
    /*TAB*/
    @media (--rwd-sm){
      padding-left: calc(var(--colr) * 2);
    }

    /*ORD*/
    @media (--rwd-md){
      padding-left: calc(var(--colr) * 1);
    }
  }

  /* COMMENTAIRES */
  &:last-child{
    margin-right: 0;
    padding-right: calc(var(--colr) * 1);
  }
}

.info-item__data{
  display: block;
  margin-top: .2em;
  @mixin text-info-data;
}

/* NAVIGATION DANS L'ART */
.post-nav{
  position: absolute; /* pour ie */
  position: sticky;
  top: 100%;
  width: 100%;
  transform: translate3d(0, -100%, 0);
  z-index: 5000;
}

.post-nav__progress{
  position: absolute;
  bottom: 0;
  width: 100%;
}

.post-nav__progress__bar{
  height: .5em;
  width: 100%;
  background: var(--color-relief);
  transform-origin: 100% 50%;
}

.post-nav__list{
  position: absolute;
  bottom: 1em;
  float: none;
  width: auto;
  right: calc(var(--col) + .8%);
  text-align: right;
}

.post-nav__item{
  display: block;
  margin: .5em 0;
  @mixin text-subtitle;
}

/* SOCIAL BOUTONS */
.share-button{ cursor: pointer; }
.input-button{
  display: none;

  &:checked ~ .input-button__toshow{
    max-height: 8em; /* -> 2 x 3els + marge */
   }
}
.input-button__toshow{
  list-style: none;
  max-height: 0;
  overflow: hidden;
  float: none;
  width: 100%;
  transition: all .4s ease;

  & > li{
    margin: .5em 0;

  }
}


/****************************
****** ARTICLE BLOG *********
*****************************/

/* DISCLAIMER */
.disclaimer{
  position: relative;
  float: none;
  background: var(--color-contrast);
  padding: 4.5rem 2rem 2.5rem;
  box-sizing: border-box;
  margin-bottom: 1em;

  /*TAB*/
  @media (--rwd-sm){
    width: 100%;

    &:before{
      content: "Note";
      position:absolute;
      top: 2.5em;
      left: 2.5em;
      @mixin font-title;
      font-weight: bold;
      text-transform: uppercase;
      font-size: .7em;
    }
  }
}

/* CONTAINER GLOBAL */
.content--post{
  position: relative;
  counter-reset: h3Counter;

  /* time line lecture FULL CSS !!!! */
  /*background: linear-gradient(to left bottom, #000 50%, #FFF 50%);
  background-size: 100% calc(100%);
  background-repeat: no-repeat;
  z-index: 1;*/

  /*&:before{
    content: '';
    position: fixed;
    @mixin inner-width var(--zone_md), var(--zone_md), 100;
    @mixin inner-left var(--zone_md), var(--zone_md), -15;
    top: 0;
    left: 0;
    height: 99.6%;
    background: var(--color-wording);
    z-index: -1;
  }*/

  /* FIN xtime line lecture FULL CSS !!!! */

  /*& p{
    margin-bottom: 5em;
  }*/

  & li{
    &:last-child{
      margin-bottom: 3em;
    }
  }

  /* premier titre interne */
  & .toc + h3{
    margin-right: 0;
    clear: none;

    /*TAB*/
    @media (--rwd-sm){
      padding-right: calc(var(--col) * 2.85);
      margin: 1em 0 1.2em;
    }
  }

  /* titre de premier niveau interne */
  & h3{
    display: block;
    margin: 2em 0 1em;

    /*TAB*/
    @media (--rwd-sm){
      margin: 2em 0 3em;
    }

    /* qui n'est pas le titre du toc */
    &:not(.toc__title){
      @mixin text-title-maj;
      /*margin: 3em 0 0; NIVEAU */
      counter-increment: h3Counter;
      counter-reset: h4Counter;
    }

    /* espace entre un texte et un h3 et son texte accolé */
    & + p{
      margin-top: 3em;

      /*TAB*/
      @media (--rwd-sm){
          margin-top: 4em;
      }
    }

    /* retour auto pour le premier titre h3 après sommaire */
    /*&:nth-child(2){ width: auto; }*/

    /* subtitle de titre interne -> sauf le nom de l'article du toc */
    & + span:not(.aside__title),
    & + div:not(.aside__title),
    & > span{
      @mixin text-subtitle;
      display: block;
      /*margin: 1em 0 2em;*/
      text-transform: none;

      /*TAB*/
      /*@media (--rwd-sm){
        margin: 1em 0 10em;
      }*/
    }
  }

  /* titre de second niveau dans l'article */
  & h4{
    display: inline-block;
    /*width: 100%;*/
    @mixin text-title-long;
    margin: 1em 0 .9em;
    padding-left: 2em;
    counter-increment: h4Counter;
    clear: both;

    /*TAB*/
    @media (--rwd-sm){
      padding-left: 0;
      @mixin inner-left var(--zone_md), var(--zone_md), 30;
    }
  }
}


/****************************
******* COMPTEUR HN *********
*****************************/

/* compteur titre */
.content--count h4{
  /* numérotation */
  &::before{
    content: counter(h3Counter) "." counter(h4Counter);
    position: absolute;
    display: block;
    top: 0;
    left: 0;
    width: calc(var(--colr) * 2);
    text-align: left;

    /*TAB*/
    @media (--rwd-sm){
      width: var(--colr);
      left: calc(var(--colr) * -2 );
      text-align: right;
    }
  }
}


/****************************
****** ASIDE BLOG *********
*****************************/

.aside{
  width: 100%;
  margin: 4em 0;
  @mixin text-subtitle;

  /* ligne de sep au mobile */
  &::before,
  &::after{
    content: "";
    position: absolute;
    display: block;
    top: -2.5rem;
    left: 0;
    height: var(--line-width);
    width: calc(100% + 1em);
    background: var(--color-contrast);

    /*TAB*/
    @media (--rwd-sm){
      display: none;
    }
  }

  &::after{
    top: auto;
    bottom: -2.5rem;
  }

  /*TAB*/
  @media (--rwd-sm){
    margin: .5em 0 0;
    float: left;
    @mixin inner-width var(--zone_md), var(--zone_md), 15;
    @mixin inner-left var(--zone_md), var(--zone_md), 10;
    text-align: right;
  }

  & p,
  & ul {
    width: 100%;
  }

  /* liste à puce aside */
  & ul{
    padding-left: 1.3em;
    & li{
      @mixin text-subtitle;
      margin: 1em 0;
    }
  }

  /* image aside */
  & > img{
    /*TAB*/
    @media (--rwd-sm){
      @mixin inner-width var(--zone_md), 70, 15;
      @mixin inner-left var(--zone_md), 70, -5;
    }

    /*ORD*/
    @media (--rwd-md){
      @mixin inner-width var(--zone_md), 70, 25;
      @mixin inner-left var(--zone_md), 70, -10;
    }
  }
}

/* ASIDE LONG POUR CONTENU -> toujours avant le p */
.aside--long{
  width: 100%;
  text-align: left;
  margin-bottom: 2em;
  clear: both;

  /*TAB*/
  @media (--rwd-sm){
    @mixin inner-width var(--zone_md), var(--zone_md), 20;
    @mixin inner-right var(--zone_md), var(--zone_md), 10;
    @mixin inner-left var(--zone_md), var(--zone_md), 0;

    /*& + p{
      @mixin inner-width var(--zone_md), var(--zone_md), 35;
      /*float: none;*/
    /*}*/
  }
}

/* TITRE EN ASIDE */
.aside__title{
  display: block;
  @mixin text-content-title;
  margin: 0 0 1em;
  line-height: 1.3;
}

/* DONNEE CLEF ASIDE */
.aside--key{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: wrap;

  /*TAB*/
  @media (--rwd-sm){
    flex-direction: column;
  }

  /* action sur les blocks de données */
  & > *{
    flex-basis: 50%;
    padding-left: var(--colr);
  }
}

/* DONNEE CLEF ASIDE -> DATA */
.aside__data{
  display: block;
  font-size: 3em;
  font-weight: 400;
  margin-bottom: 26%;
}

/* TABLE OF CONTENT */
.toc{
  margin-bottom: 8em;

}

.toc + h3 + span + p,
.toc + h3 + div + p,
.toc + h3 + p,
.toc + p
{
  margin-top: 0;
  /*TAB*/
  @media (--rwd-sm){
    /*@mixin inner-width var(--zone_md), var(--zone_md), 35;*/
    /*float: none;*/
  }
}

/* TOC LISTE DES PARTIES */
.toc__list{
  & li {
    display: block;
    font-size: 1em;
    font-weight: 500;
    line-height: 1.2;
    margin: 1.5em 0;

    /* ORD */
    @media (--rwd-md){
      margin: 1.2em 0;
    }

    &:last-child{
      margin-bottom: 3em;

      /* ORD */
      @media (--rwd-md){
        margin-bottom: 2em;
      }
    }
  }
}
.toc__item{
  & > a:after {
    display: none;
  }
}
.toc__item__sub{
  margin-top: 1em;
}

/* TITRE TOC */
.content{
  & .toc__title{
    width: 50%;
    float: none;
    @mixin text-title-toc;
    margin: 1.4em 0 1em;
    color: var(--color-relief);
  }
}


/* NOM DE l'ART H1 DANS TOC */
.toc__name{
  display: block;
  @mixin text-content-title;
  margin: 1.4em 0 .8em;
}


/****************************
******* BOUTON BLOG *********
*****************************/

/* BOUTONS */
.button-action{
  display: flex;
  align-items: center;
  padding-top: 1em;
}

/* BOUTONS EN ASIDE */
.button-action--aside{
  @media (--rwd-sm){
    flex-direction: column;
    align-items: flex-end;
  }
  @media (--rwd-md){
    flex-direction: row;
    align-items: center;
  }
}

/* TEXT EN DANS LE CONTENU -> pas aside */
.button__text--feed{
  order: 2;
  padding-left: 1em;
  padding-right: 0;
}
.button__text__action{
  display: block;
  @mixin text-content-title;
  font-weight: 700;
}

/* ICON DU BOUTONS */
.button__ico{
  display: inline-block;
  width: 4vw;
  height: 4vw;
  padding: .8em;
  border-radius: 100%;
  border: solid var(--color-relief) var(--line-width);
  box-sizing: content-box;

  /*ORD*/
  @media (--rwd-sm){
    width: 3vw;
    height: 3vw;
  }
  /*ORD*/
  @media (--rwd-md){
    order: 2;
    width: 2vw;
    height: 2vw;
  }
}

/* BOUTON RSS */
.button__ico--rss{
  &::after{
    background-image: url(../img/icons/ico-content-sprite-color@1x.png);
    background-image: url(../img/icons/ico-content-sprite-color.svg);
    background-size: 100% 245%;
    background-position: 0 0;
  }
}

/* BOUTON DDL */
.button__ico--ddl{
  &::after{
    background-image: url(../img/icons/ico-content-sprite-color@1x.png);
    background-image: url(../img/icons/ico-content-sprite-color.svg);
    background-size: 100% 245%;
    background-position: 0 62%;
  }
}

/* TEXTES DANS LES BOUTONS */
.button__text{
  flex-basis: 74%;
  padding-left: 1em;

  /*TAB*/
  @media (--rwd-sm){
    margin-top: .6em;
  }

  /*ORD*/
  @media (--rwd-md){
    padding-right: 1em;
  }
}

/* BOUTONS DDL DANS LE CONTENU -> pas aside */
.button__ddl--feed{
  order: 1;
}



/****************************
****** IMAGES BLOG *********
*****************************/

/* GESTION IMAGE */
.img-full{
  min-width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  object-fit: cover;
  object-position: center;
}

/* FIGURE INTERNE */
.post-figure{
  display: inline-block;
  width: 100%;
  margin: 5em 0 5em;
  clear: both;

  /*TAB*/
  @media (--rwd-sm){
    margin: 5em 0 12em;
  }
}

/* FIGURE PLEINE LARGEUR */
.post-figure--full{
  & > img{
    @mixin inner-width var(--zone), var(--zone), 100;
    @mixin inner-left var(--zone), var(--zone), -var(--zone);
    /*TAB*/
    @media (--rwd-sm){
      @mixin inner-width var(--zone_sm), var(--zone_sm), 100;
      @mixin inner-left var(--zone_sm), var(--zone_sm), -var(--zone_sm);
    }
    /*TAB*/
    @media (--rwd-md){
      @mixin inner-width var(--zone_md), var(--zone_md), 100;
      @mixin inner-left var(--zone_md), var(--zone_md), -var(--zone_md);
    }
  }
}

/* INTITULE IMAGE */
.post-figcaption{
  position: relative;
  @mixin inner-width 10, 10, 80;
  padding: 9% 3% 6%;
  background: var(--color-relief);
  color: var(--color-wording);
  transform: translate3D(0, -30%,0);

  /*TAB*/
  @media (--rwd-sm){
    position: absolute;
    @mixin inner-width var(--zone_md), var(--zone_md), 20;
    bottom: 0;
    @mixin inner-left var(--zone_md), var(--zone_md), 45;
    transform: translate3D(0, 30%,0);
  }
}

/* TITRE INTITULE IMAGE */
.figcaption__title{
  display: block;
  @mixin text-content-title;
  text-transform: uppercase;
}

/* DESC IMAGE */
.figcaption__content{
  display: block;
  width: 100%;
  margin-top: 1em;
  padding-right: 20%;
  @mixin text-content;
  line-height: 1.25;
}


/****************************
***** GESTION DES IMAGES ****
*****************************/

/* BLOCK SIDE IMAGE - TEXTE SIDE STYLE */
/*
  === COMPORTEMENT
  --> image aside normale : touche le bord
  --> image normale : 1 col du bord
  --> image sticky : 1 col du bord et fixe au scroll
  --> image full : touche bord et fixe scroll
  --> image fit : 1 col du bord et fixe scroll
*/

/* BLOCK DOUBLE CONTENU */
.side-content{
  display: flex;
  align-items: center;
  float: left;
  @mixin inner-width var(--zone), var(--zone), 100;
  @mixin inner-left var(--zone), var(--zone), -10;
  flex-direction: column;
  @mixin inner-width var(--zone), var(--zone), 100;
  @mixin inner-left var(--zone), var(--zone), -var(--zone);

  /*TAB*/
  @media (--rwd-sm){
    align-items: flex-start;
    margin-top: 8em;
    margin-bottom: 6em;
    flex-direction: row;
    @mixin inner-width var(--zone_sm), var(--zone_sm), 100;
    @mixin inner-left var(--zone_sm), var(--zone_sm), -var(--zone_sm);
  }

  /*ORD*/
  @media (--rwd-md){
    @mixin inner-width var(--zone_md), var(--zone_md), 100;
    @mixin inner-left var(--zone_md), var(--zone_md), -var(--zone_md);
  }
}

/* FIGURE DU DOUBLE SIDE */
.side-content__figure{
  width: 100%;
  margin-bottom: 5em;

  /*TAB*/
  @media (--rwd-sm){
    width: calc(var(--colr) * 9);
  }

  /* image interne */
  & > img{
    width: 100%;
  }
}

/* TEXTE DU DOUBLE SIDE */
.side-content__text{
  align-self: center;
  width: calc(var(--colr) * 16);
  box-sizing: content-box;

  /*TAB*/
  @media (--rwd-sm){
    width: calc(var(--colr) * 7);
    padding-left: var(--colr);
    padding-right: calc(var(--colr)* 2);
    margin-top: 5em;
  }

  /*ORD*/
  @media (--rwd-md){
    padding-right: calc(var(--colr)* 3);
  }

  /* text interne */
  & p{
    margin-left: 0;
    float: none;
    width: auto;
  }
}

/* CONTENU FIXE AU SCROLL */
.is-sticky{
  /*TAB*/
  @media (--rwd-sm){
    position: sticky;
    top: 5%;
  }

  /* texte qui suit le block fixe */
  & + .side-content__text{
    width: calc(var(--colr) * 16);

    /*TAB*/
    @media (--rwd-sm){
      width: calc(var(--colr) * 10);
    }

    /*ORD*/
    @media (--rwd-md){
      width: calc(var(--colr) * 7);
    }
  }
}

/* IMG NORMALE STICKY */
figure.is-sticky:not(.side-content__figure--full){
  margin-left: var(--colr);
}

/* IMAGE PLACE A DROITE -> INVERSE NORM */
.side-content--right{

  /* image à droite */
  & figure{
    order: 2;
    width: calc(var(--colr) * 16);

    /*TAB*/
    @media (--rwd-sm){
      width: calc(var(--colr) * 9);
    }
  }

  /* image à droite fixe */
  & figure.side-content__figure:not(.is-sticky){

    /*TAB*/
    @media (--rwd-sm){
      width: calc(var(--colr) * 10);
    }
  }

  /* image sticky non full */
  & figure.is-sticky:not(.side-content__figure--full){
    margin-right: calc(var(--colr) * 2);
    margin-left: calc(var(--colr) * 2);

    /*TAB*/
    @media (--rwd-sm){
      margin-right: var(--colr);
      margin-left: 0;
    }

    /* Texte qui suit la sticky non full */
    & + .side-content__text{
      width: calc(var(--colr) * 16);

      /*TAB*/
      @media (--rwd-sm){
        width: calc(var(--colr) * 6);
      }
    }
  }

  /* texte interne dans la disposition ima: ge à droite; */
  & .side-content__text{

    /*TAB*/
    @media (--rwd-sm){
      padding-left: calc(var(--colr) * 2);
      padding-right: var(--colr);
      width: calc(var(--colr) * 7);
    }

    /*ORD*/
    @media (--rwd-md){
      padding-left: calc(var(--colr) * 3);
      /*padding-right: var(--colr);*/
      width: calc(var(--colr) * 6);
    }
  }

  /* image à droite full */
  & .side-content__figure--full{
    width: calc(var(--colr) * 10);

    /* texte qui suit l'image à droite en full */
    & + .side-content__text{
      width: calc(var(--colr) * 6);
    }
  }
}

/* IMAGE FULL ET FIT */
.side-content__figure--full,
.side-content__figure--fit{

  /*TAB*/
  @media (--rwd-sm){
    height: 100vh;
    top: 0vh;
  }

  & img{
    height: 100%;
    object-fit: cover;
    object-position: center;
  }
}

/* IMAGE EN FIT */
.side-content__figure--fit{
  height: 90vh;
  top: 5vh;
}

/****************************
**** ENDING ARTICLE  *******
*****************************/

.post-end{
  position: relative;
  width: calc(var(--colr) * 10);
  margin-bottom: 3em;

  /*TAB*/
  @media (--rwd-sm){
    width: calc(var(--colr) * 4);
    margin: 1.5em 0;
    @mixin inner-left var(--zone_md), var(--zone_md), 30;
    clear: both;
    float: left;
  }

  &::before{
    content: '';
    position: absolute;
    display: block;
    top: 0;
    left: 0;
    width: 100%;
    height: var(--line-width);
    background: var(--color-relief);
  }

  &::after{
    content: 'thank you';
    position: absolute;
    display: block;
    top: -1.5em;
    left: 0;
    @mixin text-subtitle;
  }
}

