/* style en ligne pour auteur */
.tiles-list__tile--author{
  min-width: calc(var(--colr) * 14);

  /* TAB */
  @media (--rwd-sm){
    min-width: calc(var(--colr) * 7);
  }

  &:nth-child(2n){
    background: var(--color-default2);

    & .tile__button{
      left: auto;
      right: calc(var(--colr) / -2);
      top: 25%;
    }
  }
}
