.logo-djdb{
  position: absolute;
  width: 16vw;
  z-index: 999;
  top: var(--colr);
  left: 23vw;
  padding: .7em;

  @media (--rwd-sm){
    top: calc(var(--colr) / 2);
    left: calc(var(--colr) / 2);
    width: var(--colr);
  }

}



.menu{
  position: fixed;
  /*width: calc(100% - ( var(--colr) * 2));*/
  width: calc(var(--colr) * 4 - 4vw);

  z-index: 10; /* pour le fil de lecture article */
  z-index: 9999;
  left: var(--colr);
  top: var(--colr);
  overflow: hidden;
  transform: translate3D(0, 0, 0);

  /*TAB*/
  @media (--rwd-sm){
    /*top: calc(50% - (var(--colr) / 2));*/
    top: 50%;
    left: calc(var(--colr) / 2);
    width: calc(var(--colr) * 1.5);
    color: white;
    transform: translate3d(0, -50%, 0);
  }

  /*ORD*/
  @media (--rwd-md){
    /*position: fixed;
    top: 50%;
    left: calc(var(--colr) / 2);*/
    width: calc(var(--colr) );
  }

  & a{
    color: white;
  }
}

.menu__action{
  position: relative;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 10;

  /*TAB*/
  @media (--rwd-sm){

    display: inline-block;
    /*width: 50%;*/
    cursor: pointer;
    z-index: 10;
  }
}


.menu-trigger{
  width: 100%;
  height: calc(var(--colr) * 4 - 4vw);
  background: var(--color-relief);
  border: solid var(--color-relief) 1px;
  cursor: pointer;
  outline: none;
  border-radius: 0;
  transform-origin: 0 0;

  /*TAB*/
  @media (--rwd-sm){
    position: relative;
    display: inline-block;
    top: 2px; /* fix espace entre bouton et nav */
    width: 100%;
    height: calc(var(--colr) * 1.5);
  }

  /*ORD*/
  @media (--rwd-md){
    height: var(--colr);
  }
}

.menu-trigger__ico{
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  top: 1em;
  left: 1em;
  /*height: calc(100% - 3em);*/
  height: 50%;
  width: calc( var(--colr) * 2);
  transform-origin: 50% 0;
  pointer-events: none;
  transition: all .2s ease;

  /*TAB*/
  @media (--rwd-sm){
    width: calc( var(--colr) );
  }

  /*ORD*/
  @media (--rwd-md){
    /*height: calc(var(--colr) - 2.5em);*/
    /*height: 50%;*/
    width: calc( var(--colr) - 2em );
  }
}

.menu-trigger__ico__bar{
  position: relative;
  display: inline-block;
  height: calc(var(--line-width) * 1.5);
  width: 100%;
  background: white;
  border-radius: var(--line-width);
  transform-origin: 0 50%;
  transition: transform .4s;/* pour ralentir les barres qui se ferment */

  &:nth-child(1){
    width: 60%;
  }
  &:nth-child(2){
    width: 80%;
  }
  &:nth-child(3){
    width: 40%;
  }
}

.menu-nav{
  position: relative;
  width: 100%;
  top: -4px;
  overflow: hidden;
  background: var(--color-relief);
  /*transform: scaleY(0);*/
  height: 0;
  transform-origin: 50% 0;
  z-index: 5;
}

.menu-nav__wrapper{
  padding: 5em calc(var(--unit) * 5) calc(var(--unit) * 2) calc(var(--unit) * 15);

  /*TAB*/
  @media (--rwd-sm){
    padding: calc(var(--unit) * 2 + 3vw ) calc(var(--unit) * 3) calc(var(--unit) * 2) calc(var(--unit) * 2) calc(var(--unit) * 2);
  }
}


.menu-nav__list{
  display: flex;
  flex-direction: column;
  list-style: none;
  text-transform: lowercase;
  width: 100%;
  float: none;
}
.menu-nav__langs{
  position: absolute;
  bottom: 2.8em;
  left: 2.8em;
  height: 50%;
  width: auto;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  list-style: none;
  font-size: 1.5em;
  float: none; /*pour ul de text */
  opacity: 0;
  visibility: hidden;
  transform: translate3d(0,-20px, 0);

  /*TAB*/
  @media (--rwd-sm){
    position: relative;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    font-size: .8em;
    margin: calc(var(--unit) * 2.5) 0 var(--unit);
    width: 100%;
    top: auto;
    left: auto;
    bottom: auto;
  }
}

.menu-nav__lang{
  margin : 0;
  font-size: .9em;
}

.menu-nav__list__item{
  position: relative;
  margin: calc(var(--unit) / 4) 0;
  opacity: 0;
  visibility: hidden;
  transform: translate3d(0,-20px, 0);
  text-align: right;
  font-size: 3em;

  /*TAB*/
  @media (--rwd-sm){
    margin: calc(var(--unit) / 2.5) 0;
    text-align: inherit;
    font-size: inherit;
  }


  & > a {
    display: block;
    text-decoration: none;
    transition: all .4s ease;
    &:after{
      display: none;
    }
  }

  &:before{
    content: '';
    position: absolute;
    display: block;
    height: var(--line-width);
    width: .4em;
    top: 50%;
    left: 0;
    background: var(--color-wording);
    transform-origin: 0 50%;
    transform: scaleX(0);
    transition: all .2s ease;

  }

  &:hover{
    &:before{
      transform: scaleX(1);
      transition-duration: .3s;
      transition-delay: .2s;
    }

    & > a{
      transform: translate3d(.8em, 0, 0);
    }
  }
}


