/* Composant radio */
.c-radio{
  & > *{
    /* replacer la lang 2 après le separator */
    &:nth-child(2){
      order: 3;

      &:hover{
        &~.c-radio__separator{
          transform: translate3d(0, 13%, 0) scaleY(.8);
          @media (--rwd-sm){
            transform: translate3d(13%, 0, 0) scaleX(.8);
          }
        }
      }
    }

    &:first-child{
      &:hover{
        &~.c-radio__separator{
          transform: translate3d(0, -13%, 0) scaleY(.8);
          @media (--rwd-sm){
            transform: translate3d(-13%, 0, 0) scaleX(.8);
          }
        }
      }
    }
  }
}

.c-radio__separator{
  display: block;
  height: 100%;
  width: var(--line-width);
  flex-grow: 2;
  order: 2;
  transition: all .4s ease;

  /*TAB*/
  @media (--rwd-sm){
    height: var(--line-width);
    width: 100%;
  }

  &:after{
    content: '';
    position: absolute;
    display: block;
    width: 100%;
    top: 15%;
    height: 70%;
    background: var(--color-wording);

    /*TAB*/
    @media (--rwd-sm){
      width: 60%;
      height: 100%;
      left: 20%;
      top: inherit;
    }
  }
}
