/*HEADER ET FILTRE */
.list__intro{
  position: relative;
  padding-top: 18em;
  padding-left: calc(var(--colr) * 2);
  margin-bottom: 22em;

  /*TAB*/
  @media (--rwd-sm){
    padding-left: calc(var(--colr) * 4);
  }
}

/* NOM DE PAGE EN GRAS SUR LA LISTE */
.list__name{
  position: absolute;
  width: 94%;
  bottom: -10%;
  margin-left: calc(-var(--colr));
  /*@mixin inner-left 10, 0, -5;*/

  /*TAB*/
  @media (--rwd-sm){
    width: 100%;
    margin-left: calc(-var(--colr) * 2);
  }
}

.list__intro__title{
  width: calc(var(--colr) * 14);

  /*TAB*/
  @media (--rwd-sm){
    width: calc(var(--colr) * 6);
  }
  /*ORD*/
  @media (--rwd-md){
    width: calc(var(--colr) * 4);
  }
}

/* PARTIE LISTE LES ARTICLES */
.list{
  display: flex; /* creer une liste non hirizontale avec decallage */
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  margin-top: 4em;

  /*TAB*/
  @media (--rwd-sm){
    flex-direction: row;
  }
}

/* ARTICLE DANS UNE LISTE */
.list__article{
  min-width: calc(var(--colr) * 6); /* a changer pour animation */
  min-height: 60vmin;
  margin-bottom: 5em;
  /*background: var(--color-default2);*/

  /*TAB*/
  @media (--rwd-sm){
    min-height: 30vw;
  }

  &:nth-child(odd){
    /*background: var(--color-default);*/

  /* placement de la date de l'autre cote - autre date dans see more components */
    & .article__date{
      /*TAB*/
      @media (--rwd-sm){
        left: auto;
        right: 0;
        transform: translate3d(25%, 0, 0) rotate(-90deg);
        transform-origin: 100% 100%;
      }
    }
  }
  &:nth-child(even){
    /* placer le bouton noir de l'autre coté */
    & .article__button{
      left: initial;
      right: calc(var(--colr) / -2);
    }
  }

  /* lien des art*/
  & > a {

    display: inline-block;
    width: 100%;
    height: 100%;

    /*TAB*/
    @media (--rwd-sm){
      position: absolute;
    }
  }
}
