
  /****************************
  ******* FONTS & TEXTS ******
  *****************************/

  @define-mixin font-title {
    font-family: "Montserrat", Futura, Verdana, Arial, sans-serif;
  }

  @define-mixin font-content {
    font-family: "Poppins", "Gill Sans", "Gill Sans MT", Tahoma, system-ui, sans-serif;
  }

  @define-mixin font-code{
    font-family: Consolas, Monaco, Lucida Console, monospace;
  }


  /**** components ****/
  /*** STYLE LONG TITRE ***/
  @define-mixin text-title-long {
    @mixin font-content;
    font-weight: 400;
    font-size: 3rem;
    line-height: 1.4;


    @media (--rwd-md){
      font-size: 2.4rem;
    }
  }

  /*** STYLE TITRE EN MAJ ***/
  @define-mixin text-title-maj{
    @mixin font-title;
    font-weight: 700;
    font-size: 4rem;
    letter-spacing: .04rem;
    text-transform: uppercase;

    @media (--rwd-sm){
      font-size: 3rem;
    }

    @media (--rwd-md){
      font-size: 2.5rem;
    }
  }

  /*** STYLE INFOS DATA ***/
  @define-mixin text-info-data {
    @mixin font-content;
    font-weight: 400;
    font-size: 5rem;
    line-height: 1.3;

    @media (--rwd-sm){
      font-size: 3rem;
    }

    @media (--rwd-md){
      font-size: 2.4rem;
    }
  }

  /*** STYLE PETIT TITRE EN GRAS***/
  @define-mixin text-content-title {
    @mixin font-title;
    font-weight: 700;
    font-size: 2.5rem;
    letter-spacing: .04em;

    @media (--rwd-sm){
      font-size: 1.6rem;
    }

    @media (--rwd-md){
      font-size: 1.4rem;
    }
  }

  /*** STYLE TOC TITRE ***/
  @define-mixin text-title-toc {
    @mixin font-content;
    font-weight: 400;
    font-size: 2.2rem;
    line-height: 1.1;

    @media (--rwd-sm){
      font-size: 1.8rem;
    }

    @media (--rwd-md){
      font-size: 2rem;
    }
  }

  /*** STYLE CONTENU ***/
  @define-mixin text-content {
    @mixin font-content;
    font-weight: 300;
    font-size: 1.8rem; /*1.8em;*/
    line-height: 1.7;
    letter-spacing: .02em;

    @media (--rwd-sm){
      font-size: 1.5rem;
    }

    @media (--rwd-md){
      font-size: 1.11rem;
    }
  }

  /*** STYLE PETIT SOUS TITRE ***/
  @define-mixin text-subtitle {
    @mixin font-content;
    font-weight: 400;
    font-size: 1.8rem; /*1.8em;*/
    line-height: 1.4;

    @media (--rwd-sm){
      font-size: 1.2rem;
    }

    @media (--rwd-md){
      font-size: .9rem;
    }
  }

  /*** STYLE BOUTON ARTICLES ***/
  @define-mixin text-title-article {
    @mixin font-content;
    font-weight: 400;
    font-size: 1.8rem; /*1.8em;*/
    line-height: 1.4;

    @media (--rwd-sm){
      font-size: 1rem;
    }

    @media (--rwd-md){
      font-size: 1.2rem;
    }
  }

  /*** STYLE FAT TITLE PAGE ***/
  @define-mixin text-fat-title {
    @mixin font-title;
    font-weight: 900;
    font-size: 12vw; /*1.8em;*/
    line-height: 1;

    @media (--rwd-sm){
      /*font-size: 1.2rem;*/
    }

    @media (--rwd-md){
      /*font-size: .9rem;*/
    }
  }

  /*** STYLE FAT TITLE PAGE ***/
  @define-mixin text-author-caption {
    @mixin font-title;
    font-weight: 300;
    font-size: 2.8vw; /*1.8em;*/
    line-height: 1;

    @media (--rwd-sm){
      font-size: .8vw; /*1.8em;*/
    }

    @media (--rwd-md){
      /*font-size: .9rem;*/
    }
  }


  /****************************
  ******* WIDTH & MARGINS ******
  *****************************/

  /*** STYLE % RELATIF PARENT -> marge gauche ***/
  @define-mixin inner-left  $marge1 : 0, $marge2: 0, $width: 100 {
    margin-left: calc( $(width)% * (100 / (100 - ($marge1 + $marge2))));
  }

  /*** STYLE % RELATIF PARENT -> marge droite ***/
  @define-mixin inner-right  $marge1 : 0, $marge2: 0, $width: 100 {
    margin-right: calc( $(width)% * (100 / (100 - ($marge1 + $marge2))));
  }

  /*** STYLE % RELATIF PARENT -> width ***/
  @define-mixin inner-width  $marge1 : 0, $marge2: 0, $width: 100 {
    width: calc( $(width)% * (100 / (100 - ($marge1 + $marge2))));
  }


  /****************************
  *********** LAYOUT **********
  *****************************/

@define-mixin wrapper {
  padding: 0 calc(var(--col) * 2);

  /*ORD*/
  @media (--rwd-md){
    padding: 0 calc(var(--col) * 3);
  }
}
