/****************************
******** WRAPPER ************
*****************************/

.wrapper{
  display: flex;
  flex-wrap: wrap;

  /*CLEARFIX HACK */
  &:after {
    content: "";
    display: table;
    clear: both;
  }
}
.gutters{
  padding-left: calc(var(--col) * 2);
  padding-right: calc(var(--col) * 2);
}


/****************************
******** COMMONS ************
*****************************/

.insecable{
  white-space: nowrap;
}

/****************************
******** WORDING ************
*****************************/

.wording{
  position: absolute;
  display: inline-block;
  left:0;
  @mixin font-title;
  font-size: 15rem;
  font-weight: 900;
  line-height: .8;
  color: var(--color-contrast);
  text-transform: uppercase;
  z-index: -1;
  max-width: 100%;
  overflow: hidden;

  @media (--rwd-sm){
    font-size: 20rem;
  }
}
.wording--inner{
  top: -.5em;
}
.wording--bottom{
  bottom: 0;
  transform: translateY(30%);
}
.wording--top{
  top: 0;
  transform: translateY(-30%);
}
.wording--fat{
  font-size: 22em;
}

/****************************
******** TITLES ************
*****************************/
/*** TITRES H3 avec sous titre ***/
.section-title{
  width: calc(var(--colr) * 5);
}
.section-subtitle{
  width: calc(var(--colr) * 4);
  @mixin text-subtitle;
}

/*** TITRES H2 LONG EN INTERSECTION -> same intersection title ***/
.title-long{

}


/****************************
********** PAGES ************
*****************************/

/* partie groupé de titre et paragraphe */
.text-part{
  & h2, & p{
    float: none;
  }
}

/* liste horizontale de tiles */
.tiles-list{
  position: relative;
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  align-items: flex-start;
  overflow-x: scroll;
  overflow-y: hidden;
  margin-bottom: 8em;
  margin-top: 12em;
  padding-bottom: 3em;
  @mixin inner-width var(--zone_sm), var(--zone_sm), 100;
  @mixin inner-left var(--zone_sm), var(--zone_sm), -10;
  padding-left: calc(var(--colr) * 2);

  /* TAB */
  @media (--rwd-sm){
    @mixin inner-width var(--zone_md), var(--zone_md), 100;
    @mixin inner-left var(--zone_md), var(--zone_md), -15;
    margin-bottom: 12em;
  }

  /* espace avant la fin du scroll pour ne pas faire de retour avantet arrière */
  & > *:last-child{
    &:after{
      content: '';
      position: relative;
      display: block;
      width: calc(var(--colr) * 3);
      height: 1px;
      margin-left: 100%;
      background: transparent;
    }
  }
}

/* vers la gauche */
.tiles-list--rtl{
  direction:rtl;
  padding-right: calc(var(--colr) * 3);
  padding-left: initial;
  text-align: left;

  /* inversion espace avant retour arrière au scroll */
  & > *:last-child{
    &:after{
      margin-left: calc(var(--colr) * -3);
    }
  }
  & .tiles-list__tile{
    margin-left: calc(var(--colr) * 2);
    margin-right: initial;

    /* TAB */
    @media (--rwd-sm){
      margin-left: var(--colr);
    }
  }
}

/* texte en regard d'une liste */
.tiles-list__text{
  align-self: center;
  direction: ltr;
}

/* ARTICLE EN TUILE */
.tiles-list__tile{
  position: relative;
  min-width: calc(var(--colr) * 16);
  margin-right: calc(var(--colr) * 2);
  min-height: 40em;
  background: var(--color-default);
  direction: ltr;

  /* TAB */
  @media (--rwd-sm){
    margin-right: var(--colr);
    min-width: calc(var(--colr) * 6);
  }
}

/* bouton noir accompagnant les articles */
.tile__button{
  position: absolute;
  display: flex;
  align-items: stretch;
  background: var(--color-relief);
  color: var(--color-wording);
  width: 90%;
  top: 75%;
  transform: translate3d(0, -50%, 0);
  left: calc(var(--colr) / -2);
}

/* mettre les elements cote cote */
.tile__button--row{
  flex-direction: row-reverse;
}

/* DISTRIBUTION HORIZONTALE */
.tile__button__title{
  max-width: 50%;
  margin: 1em;
}
.tile__button__info{
  width: 100%;
  padding-left: 1em;
}

/* liste d'icon en row */
.icon-list{
  display: flex;
  list-style: none;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  height: 100%;
  padding: 2em 0;
}

/* icon dans la liste */
.icon-list--item{
  width: 10vw;
  height: 10vw;
  flex-shrink: 1;
  margin-right: .5em;

  /*TAB*/
  @media (--rwd-sm){
    width: 4vw;
    height: 4vw;
    margin-right: .8em;
  }
  /*ORD*/
  @media (--rwd-md){
    width: 3vw;
    height: 3vw;
    margin-right: .5em;
  }

  & > a {
    display: inline-block;
    height: 100%;
    width: 100%;
  }
}

/* affoche nom lien quand pas de css */
.ico-name{ display: none; }

/****************************
******** CONTENT ************
*****************************/

/* CONTAINER GLOBAL */
.content{
  display: flow-root;
  width: 100%;
  margin: 6em 0;
  @mixin wrapper;
  box-sizing: border-box;
  counter-reset: TitleCounter;

  /* texte suivant une image */
  & img + p{ margin-top: 2.5em; }

  /* texte et citation */
  & p,
  & q{
    display: block;
    margin-left: 0;
    margin-bottom: 2.5em;
    overflow: hidden;
  }

  & li{
    margin: 0 1em 1.3em;
  }

  & h2{
    /*TAB*/
    @media (--rwd-sm){
      width: calc(var(--colr) * 10);
    }
    /*ORD*/
    @media (--rwd-md){
      width: calc(var(--colr) * 5);
    }
  }

  /* citation et ensemble de citation */
  & q,
  & blockquote > p{
    position: relative;
    @mixin inner-left var(--zone_md), var(--zone_md), 15;
    padding-left: 1.6em;
    @mixin text-content-title;
    color: var(--color-default);
    line-height: 1.35;

    &:before{
      content: "";
      position: absolute;
      display: block;
      width: calc(var(--line-width) * 2);
      height: 100%;
      top: 0;
      left: .2em;
      background: var(--color-default);
    }

    &:after{ display: none; }
  }

  /* ensemble de citation */
  & blockquote{
    clear: both;

    /* auteur */
    & cite{
      position: absolute;
      display: block;
      left: 0;
      top: 42px;
      @mixin inner-width var(--zone_md), var(--zone_md), 26;
      @mixin text-subtitle;
      text-align: right;
      color: var(--color-default);
      font-style: normal;
    }

    /* phrase de citation */
    & > p{
      overflow: visible;

      &:before{
        content: "";
        display: none;
      }
    }
  }
}

/* icone de quote en blockquotes */
.blockquote-icon{
  position: absolute;
  width: 45px;
  top: .2em;
  right: 105%;
}

/* titre de premier niveau interne */
.content--page h2,
.content--post h3,
.text-side h2,
.section-title{
  width: 100%;
  clear: both;

  @media (--rwd-sm){
    @mixin inner-right var(--zone_md), var(--zone_md), 5;
    @mixin inner-width var(--zone_md), var(--zone_md), 25;
    float: left;
  }

  /* paragraphe suiavnt le titre -> a cote sur desktop */
  & + p{

    /*TAB*/
    @media (--rwd-sm){
      @mixin inner-width var(--zone_md), var(--zone_md), 40;
      /*float: none;*/
    }
  }
}



/* titre de second niveau dans l'article */
.content--page h3,
.under-title{
  @mixin text-title-long;
  margin: 1.5em 0 .6em;
  padding-left: 2em;
  counter-reset: titleCounter3;
  counter-increment: TitleCounter2;
  clear: both;

  /*TAB*/
  @media (--rwd-sm){
    padding-left: 0;
    @mixin inner-left var(--zone_md), var(--zone_md), 30;
  }
}


/* titre de troisieme niveau dans l'article */
.content--page h4,
.under-sub-title{
  @mixin text-title-long;
  margin-left: 0;
  padding-left: 2em;
  counter-reset: titleCounter4;
  counter-increment: TitleCounter3;
  clear: both;

  /*TAB*/
  @media (--rwd-sm){
    font-size: 1.6rem;
    padding-left: 0;
    @mixin inner-left var(--zone_md), var(--zone_md), 30;
  }
}



/****************************
******* DEFAULT PAGE ********
*****************************/
/* INTRO PAGE SIMPLE */
.main__intro{
  width: 100%;
  min-height: 60vh;
  background: var(--color-default2);
  overflow: auto;
}

.main__header{
  margin: 20vh 0 5em;
  width: 100%;
}

.main__header--long{
  margin: 20vh 0 30vw;
}

.main__header__title,
.main__header__subtitle{
  width: calc(var(--col) * 16);
  margin-left: calc(var(--col) * 2);
  text-align: center;
  float: none; /* Pour grille */

  /*TAB*/
  @media (--rwd-sm){
    width: calc(var(--col) * 15);
    margin-left: calc(var(--col) * 4);
    text-align: left;
  }
  /*ORD*/
  @media (--rwd-md){
    width: calc(var(--col) * 6);
    margin-left: calc(var(--col) * 7);
    text-align: left;
  }
}

.main__header__subtitle{  margin-top: 1.5em; }

.main__header__page{
  position: relative;
  display: block;

  color: var(--color-wording);
  text-align: center;
  @mixin text-fat-title;
  text-transform: uppercase;
  letter-spacing: 6px;

  /*TAB*/
  @media (--rwd-sm){
    width: calc(var(--col) * 16);
    margin-left: calc(var(--col) * 3);
  }

}

/* filtre article par ex */
.filter{
  float: none; /* pour grille ... */
}

/* liste d'elements cote cote*/
.content__icolist{
  display: flex;
  flex-direction: row;
  margin-left: 0;
  margin-bottom: 3em;
  overflow: hidden;
  justify-content: flex-start;
  flex-wrap: wrap;
  @mixin text-content;
  font-size: 1.8rem;

  /*TAB*/
  @media (--rwd-sm){
      @mixin inner-left var(--zone_md), var(--zone_md), 20;
      @mixin inner-width var(--zone_md), var(--zone_md), 40;
      float: right;
      font-size: .9em;

      @mixin inner-left var(--zone_md), var(--zone_md), 0; /* --> suppr au dessus */
  }

  & > *{
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    padding-right: 2em;
    margin: 3em 0 1em;
  }
}

.content__icolist__item{
  padding-left: 12vw;
  box-sizing: border-box;

  /*TAB*/
  @media (--rwd-sm){
    padding-left: 4vw;
  }
}



/****************************
******* COMPTEUR HN *********
*****************************/

/* compteur titre */
.content--count:not(.content--post) h3{
  /* numérotation */
  &::before{
    content: counter(TitleCounter2) ".";
    position: absolute;
    display: block;
    top: 0;
    left: 0;
    width: calc(var(--colr) * 2);
    text-align: left;

    /*TAB*/
    @media (--rwd-sm){
      width: var(--colr);
      left: calc(var(--colr) * -2 );
      text-align: right;
    }
  }
}

/* compteur sous-titre */
.content--count:not(.content--post) h4{
  /* numérotation */
  &::before{
    content: counter(TitleCounter2) "." counter(TitleCounter3);
    position: absolute;
    display: block;
    top: 0;
    left: 0;
    width: calc(var(--colr) * 2);
    text-align: left;

    /*TAB*/
    @media (--rwd-sm){
      width: var(--colr);
      left: calc(var(--colr) * -2 );
      text-align: right;
    }
  }
}


/****************************
******** ICONS ************
*****************************/

.ico{
  position: relative;
  display: inline-block;
  border-radius: 100%;
  border: var(--line-width) solid var(--color-relief);
  /*top: 50%;
  left: 50%;*/
  /*transform: translate3D(-50%, -50%, 0);*/
  background-repeat: no-repeat;
  transition: .4s all ease;
  & > a{
    display: block;
    padding: 25%;
     &:after{
       display: none;
     }
  }

  &:hover{
    background: var(--color-relief);

    & .svg-shape{
      fill: var(--color-wording);
    }
  }
}

.svg-shape{
  transition: .4s all ease;
}

.ico--inverse{
  border-color: var(--color-wording);
  & .svg-shape{
      fill: var(--color-wording);
  }

  &:hover{
    background: var(--color-wording);

    & .svg-shape{
      fill: var(--color-relief);
    }
  }
}
