/****************************
****** SEE MORE SECTION *****
*****************************/
/* WRAPPER */
.see-more{
  @mixin wrapper;
  margin-bottom: 10em;
  z-index: 10;
}

/* content liste d'articles*/
.see-more__list{
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-top: 5em;
  padding-bottom: 5em;

  &::before{
    content: '';
    position: absolute;
    display: block;
    width: calc(var(--colr) * 11 );
    height: 100%;
    bottom: 0;
    left: calc(var(--colr) * 2);
    background: var(--color-contrast);
  }
}

/* article de see more -> 30: 7col * 5% */
.see-more__article{
  position: relative;
  @mixin inner-width var(--zone_md), var(--zone_md), 30;
  flex-basis: calc( 30% * (100 / (100 - (var(--zone_md) + var(--zone_md)))));
  min-height: 30vw;

  &:nth-child(odd){
    & .article__button{
      left: auto;
      right: calc(var(--colr) / -2);
    }

    & .article__date{
      left: auto;
      right: 0;
      transform: translate3d(25%, 0, 0) rotate(-90deg);
      transform-origin: 100% 100%;
    }
  }

  &:first-child{
    margin-top: 8em;
  }

  &:nth-child(2){
    margin-top: -8em;
  }

  & > a{
    position: absolute;
    display: block;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}

.see-more__article__img{
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  min-height: 61vmin;
  object-fit: cover;
  object-position: center;
  z-index: 25;

  /*TAB*/
  @media (--rwd-sm){
    position: absolute;
    min-height: 100%;
  }
}

.article__button{
  position: absolute;
  top: 50%;
  left: calc(var(--colr) / -2);
  width: 80%;
  padding: calc(var(--colr) / 3) calc(var(--colr) / 2);
  background: var(--color-relief);
  color: var(--color-wording);
  transform: translate3d(0, -20%, 0);
  z-index: 50;
}

.article__title{
  @mixin text-title-article;
  margin: 1em 0;
}

.article__taxo{
  @mixin text-subtitle;
}

.article__date{
  position: absolute;
  top: 5%;
  left: 0;
  transform: translate3d(-105%, 0, 0) rotate(-90deg);
  transform-origin: 100% 100%;
  text-transform: uppercase;
  z-index: 900;
}
