.c-contactForm{
  background: var(--color-relief);
  color: var(--color-wording);
  width: 100%;
  padding: 2em 0 34%;
  margin-top: -16vw;
  /*height: 48vw; /* retirer ? => pour ne pas recharger la hauteur de l'intersection -> a générer dynamiquement,  */

  /*TAB*/
  @media (--rwd-sm){
    width: calc(var(--colr) * 16);
    margin-left: calc(var(--colr) * 4);
    padding: 2em 0 18%;
  }
}

.contactform__title{
  padding: 0 calc(var(--colr) * 3);
  box-sizing: border-box;

  @media (--rwd-sm){
    padding: 0 var(--colr) 0 calc(var(--colr) * 3);
  }
}

.contactform__group{
  margin-left: calc(var(--colr) * 3);
  margin-right: calc(var(--colr) * 3);

  @media (--rwd-sm){
    margin-right: var(--colr);
  }
}

.contactform__writter{
  display: block;
  @mixin text-author-caption;
  margin-bottom: .8em;
  margin-top: 4em;

  @media (--rwd-sm){
    margin-bottom: .4em;
  }
}

.contactform__wording{
  position: relative;
  @mixin text-content;
  line-height: 1.7;
  display: inline-block;
  float: none; /* pour eviter float articles */
  word-wrap:break-word;

  @media (--rwd-sm){
    font-size: 1.5em;
  }

}
p.contactform__wording{
  width: calc(100% - var(--colr) * 3);
}

.cursor{
  position: absolute;
  width: 1px;
  height: calc(100% - .16em);
  background: var(--color-wording);
  top: .08em;
  left: 0;
}


.contactform__answer{
  display: block;
  position: relative;
  padding-left: .5em;
  @mixin text-content;
  font-weight: 400;
  outline: none;
  padding-left: 0;
  background: transparent;
  border: none;
  border-radius: 0;
  box-sizing: border-box;
  width: 100%;
  font-size: 2.5em;
  z-index: 5;
  transform-origin: 0 50%;
  border-bottom: var(--line-width) solid var(--color-wording);
  color: var(--color-wording);
  transition: border .4s ease;

  @media (--rwd-sm){
    display: inline-block;
    font-size: 1.5em;
    width: 30%;
    padding-left: 5px;
  }
}

.contactform__email{
  position: relative;
  display: block;
  /*TAB*/
  @media (--rwd-sm){
    display: inline;
  }
}
.contactform__answer__info{
  position: absolute;
  width: 100%;
  left: 0;
  top: 100%;
  font-size: 1.3em;
  line-height: 3;
  color: #FD5536;
  opacity: 0;
  transform: translate3d(0,-10px, 0);
  transition: all .4s ease;
  z-index: 0;

  @media (--rwd-sm){
    font-size: .8em;
    line-height: 4;
  }
}
.contactform__answer--wrong{
  border-bottom: var(--line-width) solid #FD5536;

  & + .contactform__answer__info{
    opacity: 1;
    transform: translate3d(0,0,0);
  }
}

.contactform__validanswer{
  display: inline-block;
  cursor: pointer;
  vertical-align: middle;
  border: var(--line-width) solid var(--color-wording);
  transition: background .4s ease;
  margin-left: 0px;
  width: 6em;
  height: 6em;
  border-radius: 6em;
  margin: 6em 0 0 calc(100% - 6em );

  @media (--rwd-sm){
    margin: 0 0 0 2em;
    width: 2.2em;
    height: 2.2em;
    border-radius: 2em;
  }

  & > svg{
    padding: 1.5em;
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    transition: stroke .4s ease;

    @media (--rwd-sm){
      padding: .5em;
    }
  }

  &:hover{
    background: var(--color-wording);
    & .ico-arrow-poly{
      stroke: var(--color-relief);
    }
  }
}

.button--error{
  border: none;
  cursor: inherit;

  & > svg{
    padding: .3em;

    & > *{
      stroke: #FD5536;
      fill: #FD5536;
    }
  }

  &:hover{
    background: inherit;
    & .ico-arrow-poly{
      stroke: inherit;
    }
  }
}

.contactform__answer--para{
  width: 100%;
  padding: 0;
  resize:vertical;

  @media (--rwd-sm){
    width: calc(100% - var(--colr) * 3);

  }
}


.c-counter{
  display: none;
  position: absolute;
  top: 9.5em;
  left: 2em;
  height: 200px;
  @mixin font-title;
  font-weight: 500;
  font-size: 1.8em;

  @media (--rwd-sm){
    display: block;
  }
}

.counter__first{
  position: absolute;
  top: 0;
  height: 1.8em;
  overflow: hidden;
}

.counter__last{
  position: absolute;
  bottom: 0;
  &:before{
    content: '';
    position: absolute;
    bottom: calc(100% + 10px);
    left: calc(50% - var(--line-width) / 2);
    display: block;
    width: var(--line-width);
    height: 100px;
    background: var(--color-wording);
  }
}

.counter__list{
  list-style: none;
  float: none;
}

.counter__item{
  @mixin font-title;
  font-weight: inherit;
  font-size: inherit
}


/******************
******* select
*******************/

/* style par défaut */
.c-select {
	display: block;
	vertical-align: middle;
	position: relative;
	text-align: left;
	z-index: 100;
	width: 100%;

	user-select: none;
  @mixin text-content;
  font-weight: 400;
  font-size: 2.5em;


  /*TAB*/
  @media (--rwd-sm){
    max-width: 38%;
    display: inline-block;
    font-size: 1.5em;
  }
}

/* accessibilité */
div.c-select:focus {
	outline: none;
}

/* desactive vue select classique */
.c-select__tool{
	display: none;
}

/* ensemble du texte container du composant */
.c-select span {
	display: block;
	position: relative;
	cursor: pointer;
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
}

.c-select__placeholder{
  padding: 0 1.5em .14em 0.5em;
  border-bottom: var(--line-width) solid var(--color-wording);
  border-color: inherit;
  transform-origin: 0 50%;
}

/* icone du texte selectionné ou top */
.c-select__placeholder::after,
.c-select .is-selected span::after {
	speak: none;
	position: absolute;
	top: 50%;
	transform: translateY(-50%);}

/* icon plus ici */
.c-select__placeholder::after {
	content: '\002B';
	right: 0.25em;
	transform: translate3d(0,-50%,0) rotate3d(0,0,1,0deg);
	transition: transform 0.5s;
}

/*icone du texte selectionné */
/*.c-select .is-selected span::after {
	content: '\2713';
  width: 1.5em;
  height: var(--line-width);
  background: var(--color-wording);
	margin-left: .5em;
}*/

/* tourner l'icone top à l'ouverture */
.c-select.c-select--is-open .c-select__placeholder::after {
  transform: translate3d(0,-50%,0) rotate3d(0,0,1,225deg);
}

/* Options */
.c-select__options {
	position: absolute;
	overflow: hidden;
	width: 100%;
	visibility: hidden;
  opacity: 0;
  transition: opacity 0.3s 0.4s, visibility 0s 0.7s;

}

/* rendre visible lesz éléments au click */
.c-select--is-open .c-select__options {
	visibility: visible;
  opacity: 1;
	transition: opacity 0.3s;
}


.c-select ul {
	list-style: none;
	margin: 0;
	padding: 0;
	width: 100%;
}


/* contenu des options */
.c-select__values span {
	position: relative;
	text-transform: uppercase;
	font-size: 66%;
	font-weight: 500;
	letter-spacing: 1px;
	padding: 1.2em 0.8em;
	opacity: 0;
  background: var(--color-relief);
  color: var(--color-disabled);
	transform: translate3d(100%,0,0);
	transition: opacity 0.3s, transform 0.3s;

}

/* icon du selectionné - on garde ? */
/*.c-select__values span::after {
	content: '';
	opacity: 0;
}
.c-select__values.is-selected span::after {
	opacity: 1;
	transition: opacity 0.3s 0.7s;
}*/

/* barre en dessous de chaque option */
.c-select__values span::before {
	content: '';
	position: absolute;
	bottom: 1px;
	left: 0;
	height: var(--line-width);
	width: 100%;
	background-color: #fff;
	transform: translate3d(200%,0,0);
	transition: transform 0.3s;
}

/* apparition du texte option */
.c-select.c-select--is-open li span,
.c-select.c-select--is-open li span::before {
	opacity: 1;
	transform: translate3d(0,0,0);
}

.c-select li:nth-child(7) span,
.c-select li:nth-child(7) span::before,
.c-select.c-select--is-open li:first-child span,
.c-select.c-select--is-open li:first-child span::before {
	transition-delay: 0s;
}

.c-select li:nth-child(6) span,
.c-select li:nth-child(6) span::before,
.c-select.c-select--is-open li:nth-child(2) span,
.c-select.c-select--is-open li:nth-child(2) span::before {
	transition-delay: 0.045s;
}

.c-select li:nth-child(5) span,
.c-select li:nth-child(5) span::before,
.c-select.c-select--is-open li:nth-child(3) span,
.c-select.c-select--is-open li:nth-child(3) span::before {
	transition-delay: 0.09s;
}

.c-select__values:nth-child(4) span,
.c-select__values:nth-child(4) span::before{
	transition-delay: 0.125s;
}

.c-select li:nth-child(3) span,
.c-select li:nth-child(3) span::before,
.c-select.c-select--is-open li:nth-child(5) span,
.c-select.c-select--is-open li:nth-child(5) span::before {
	transition-delay: 0.17s;
}

.c-select li:nth-child(2) span,
.c-select li:nth-child(2) span::before,
.c-select.c-select--is-open li:nth-child(6) span,
.c-select.c-select--is-open li:nth-child(6) span::before {
	transition-delay: 0.215s;
}

.c-select li:first-child span,
.c-select li:first-child span::before,
.c-select.c-select--is-open li:nth-child(7) span,
.c-select.c-select--is-open li:nth-child(7) span::before {
	transition-delay: 0.26s;
} /* plus de délais pour plus d'animation */


/* mise en couleur du texte hovered */
.c-select__values span:hover,
.c-select__values.cs-focus span,
.c-select__values.is-selected span {
	color: var(--color-wording);
}
