/****************************
******* INTERSECTION ******
*****************************/

.intersection{
  position: relative;
  width: 100%;
  background: var(--color-relief);
  padding-top: 1em;
  color: var(--color-wording);
}

.intersection__title{
  display: block;
  width: 100%;
  margin: 3em 0 2em;
  @mixin text-title-long;
  color: white;
  text-transform: none;
}


/****************************
******* LISTS ******
*****************************/

.intersection__list{
  margin: 0 0 6em;
  width: calc(var(--col) * 20);
  color: white;

  /*TAB*/
  @media (--rwd-sm){
    width: calc(var(--col) * 10);
    padding-right: 3ch;
    float: left;
  }

  /*ORD*/
  @media (--rwd-md){
    width: calc(var(--col) * 4);
    padding-bottom: 9em;
  }
}

.intersection__list__title{
  margin: .4em 0;
  @mixin text-content-title;

  /*ORD*/
  @media (--rwd-md){
    margin: .6em 0;
  }
}

.intersection__list__points{
  list-style: none;
  @mixin text-subtitle;
}

.intersection__list__ico{
  position: relative;
  height: 2.5em;
  width: 2.5em;
  float: left;
}

.intersection__end{
  position: relative;

  /*ORD*/
  @media (--rwd-md){
    position: absolute;
    bottom: 0;
    right: 0;
  }
}

.intersection__list--dev{
  @media (--rwd-md){
    width: calc(var(--col) * 5);
  }
}
