html{
  position: relative;
  /** set em size **/
  font-size: 15px;
  font-size: calc(0.875em + 0.25 * (100vw - 80em) / 40); /*-> 1rem */
  /*overflow-x: hidden; --> pb position sticky*/
  @mixin font-content;
}
body{
  position: relative;
  overflow-x: hidden;
}
/** desactiver hilight on tap */
*{
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

/*** selection de fond blanc ***/
::-moz-selection {
  background: var(--color-relief);
  color: var(--color-wording);
}
::selection {
  background: var(--color-relief);
  color: var(--color-wording);
}
/*** selection de fond black ***/
:matches(:matches(.intersection, .intro__header, .post-figcaption, .c-contactForm) *)::-moz-selection{
  background: var(--color-wording);
  color: var(--color-relief);
}
:matches(:matches(.intersection, .intro__header, .post-figcaption, .c-contactForm) *)::selection{
  background: var(--color-wording);
  color: var(--color-relief);
}
/*** selection de code ***/
code::-moz-selection{ background: var(--color-default); }
code::selection{ background: var(--color-default); }


h1, h2, h3, h4{
  position: relative;
  margin: 0;/*retirer quand margins appliqués sur les H*/
  padding: 0;
}

h1{
  @mixin text-title-long;
}

h2{
  @mixin text-title-maj;
  & > span{
    @mixin text-subtitle;
    display: block;
    margin: 1em 0 2em;
    text-transform: none;
  }
}

h3{
  display: inline-block;
  @mixin text-title-long;
}

h4{
  @mixin text-title-long;
  font-size: 2rem;
  margin: 0 0 .5em;

  @media (--rwd-md){
    font-size: 1.5rem;
  }
}

strong{
  font-weight: 500;
}
blockquote{
  margin: 0;
  padding: 0;
  width: 100%;
}

ul, ol, figure, dl, dd, dt, p{
  margin: 0;
  padding: 0;
}

section, aside, div, span, figure, figcaption, article, dl, ul, ol, li, nav, a, blockquote{
  position: relative;
  box-sizing: border-box;
}

p, ul li, ol li, button{
  @mixin text-content;
}

li li{
  font-size: 1em;
}

/* CONTENU ARTICLE ET PAGE SIMPLE */
p,
q,
ul{
  /*TAB*/
  @media (--rwd-sm){
    float: right;
    @mixin inner-width var(--zone_md), var(--zone_md), 40;
  }
}



a{
  color: var(--color-relief);
  text-decoration: none;

}

p, li{
  & > a{
    display: inline-block;
    text-decoration: none;
    &:after{
    content: "";
    position: absolute;
    left: 0;
    bottom: .15em;
    display: inline;
    width: 100%;
    height: 2px;
    background: var(--color-relief);
    transform-origin: 0;
    transition: all 1s cubic-bezier(.26,.91,.62,1);
    }

    &:hover{
      &:after{
        transform: scaleX(0);
      }
    }
  }
}

figure{
  & > img{
    min-width: 100%;
  }
}

address{
  font-style: normal;
}
img{
  width: 100%;
}

ol{
  list-style-type: none;
  counter-reset: listCounter;
  & li{
    display: inline-block;
    margin-bottom: 1.5em;
    counter-increment: listCounter;
    padding-left: 2em;

    &:before{
      position: absolute;
      left: .1em;
      font-size: 1em;
      font-weight: 900;
      /*line-height: 1.8;*/
      content: counters(listCounter, ".") ".";
    }
  }
  & ol li{
    margin: .2em 0;
    padding-left: 2em;
  }
}

code{
  padding: 2px 5px;
  word-wrap: break-word;
  word-spacing: normal;
  hyphens: none;
  font-variant-ligatures: none;
  @mixin font-code;
  background: var(--color-contrast);
}

hr{
  width: 100%;
  border: solid var(--color-contrast) 1px;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
    transition: background-color 5000s ease-in-out 0s;
    -webkit-text-fill-color: #fff !important;
}

input:invalid {
 box-shadow:none;
}
