/****************************
******** FOOTER ************
*****************************/

.footer{
  position: relative;
  z-index: 10;
  & .intersection{
    &:before{
      content:'';
      position: absolute;
      display: block;
      height: 28%;
      width: calc(var(--col) * 20);
      bottom: -1px;
      right: 0;
      background: white;

      /*ORD*/
      @media (--rwd-md){
        height: calc( 100% + 5px);
        width: calc(var(--col) * 6);
      }
    }
  }

  & .intersection__list{
    width: calc(var(--col) * 20);

    /*TAB*/
    @media (--rwd-sm){
      width: calc(var(--col) * 5);
    }
    /*ORD*/
    @media (--rwd-md){
      width: calc(var(--col) * 3);
    }
  }

  & .visitus{


    /*TAB*/
    @media (--rwd-sm){
      width: calc(var(--col) * 11);
    }

    /*ORD*/
    @media (--rwd-md){
      width: calc(var(--col) * 8);
    }
  }

  & .loc{
    width: calc(var(--col) * 20);
    color: var(--color-relief);

    /*TAB*/
    @media (--rwd-sm){
      float: left;
      width: calc(var(--col) * 10);
    }

    /*ORD*/
    @media (--rwd-md){
      width: calc(var(--col) * 4);
      margin-left: calc(var(--col) * 2);
      padding: 0;
    }
  }

  & .social{
    display: flex;
    justify-content: flex-start;
    width: calc(var(--col) * 20);

    /*TAB*/
    @media (--rwd-sm){
      justify-content: flex-end;
      width: calc(var(--col) * 10);
    }

    /*ORD*/
    @media (--rwd-md){
      width: calc(var(--col) * 20);
      justify-content: flex-start;
    }
  }
  & .social__item{
    &:first-child{
      /*ORD*/
      @media (--rwd-md){
        margin-left: calc(var(--col) * 16);
      }
    }
    &:not(:last-child){ margin-right: .8em; }
  }
}

.footer__links{
  display: flex;
  flex-wrap: wrap;
  width: calc(var(--col) * 20);
  padding-top: calc(var(--unit) * 4);
  padding-bottom: calc(var(--unit) * 1.5);
  @mixin text-subtitle;

  /*ORD*/
  @media (--rwd-md){
    padding-top: calc(var(--unit) * 8);
    padding-bottom: calc(var(--unit) * .5);
  }

  & > p:first-of-type{
    float: right;
    width: calc(var(--col) * 20);
    font-size: 1em;

    /*ORD*/
    @media (--rwd-md){
      width: calc(var(--col) * 14);
      order: 1;
    }
  }
}
.footer__links__nav{
  width: calc(var(--col) * 20);
  float: right;
  margin-bottom: calc(var(--unit) * 2);

  /*ORD*/
  @media (--rwd-md){
    width: calc(var(--col) * 6);
    order: 2;
  }
}
.footer__links__list{
  display: flex;
  width: 100%;
  list-style: none;

  /*ORD*/
  @media (--rwd-md){
    justify-content: flex-end;
  }

  & > li{
    &:not(:last-child){ margin-right: 4em; }
  }
}
.footer__links__item{
  display: inline-block;

  & > a {
    &:after{
      display: none;
    }
  }

  /*ORD*/
  @media (--rwd-md){
    float: right;
  }
}


/****************************
***** SOCIAL BUTTONS ******
*****************************/

.social{ list-style: none; }
.social__item{
  position: relative;
  width: 18vw;
  height: 18vw;
  float: left;
  border: solid var(--line-width) var(--color-relief);
  border-radius: 100%;

  /*TAB*/
  @media (--rwd-sm){
    width: 4.5vw;
    height: 4.5vw;
  }
  /*ORD*/
  @media (--rwd-md){
    width: 3vw;
    height: 3vw;
  }

  & a{
    display: inline-block;
    height: 100%;
    width: 100%;
  }
}

.footer-copyrigth{
  line-height: 1.1;
}
